html,
body {
  width: 100%;
  height: 100%;
  /* max-width: 1280px; */
  max-width: 1080px;
  /* max-height: 740px; */
  max-height: 790px;
  margin: auto;
  border: 1px solid var(--primary);
  font-family: "Open Sans";
  overflow:hidden;
}

::-webkit-scrollbar {
  -webkit-appearance: none;
  width: 8px;
}

::-webkit-scrollbar-thumb {
    border-radius: 4px;
    background-color: rgba(0,0,0,.5);
    box-shadow: 0 0 1px rgba(255,255,255,.5);
}


#root {
  width: 100%;
  height: 100%;
}

table {
  font-size: 0.75em;
}
.request-btn {
  font-size: 0.75em;
}

.confirm-intv {
  margin-top: 10px;
  font-size: 0.75em;
}

.container-fluid,
.main {
  width: 100%;
  margin: 0;
  padding: 0;
  /* padding-left: 5px; */
  /* padding-right: 5px; */
  height: 100%;
  overflow: hidden;
}

.row {
  /* height: 100%; */
  /* margin-left: -5px; */
  margin-left: 0;
  width: 100%;
  margin-right: 0;
  overflow: hidden;
}
.wrapper-row {
  height: 100%;
  width: 100%;
}

.main-row {
  height: calc(100% - 52px);
}

.col {
  height: 100%;
  padding-top: 5px;
  padding-bottom: 5px;
  padding: 15px;
  /* overflow-y: scroll; */
  overflow-y: hidden;
  /* -webkit-overflow-scrolling: touch; */
}

.main-region {
  height: 100%;
  width: 100%;
  padding-left: 0;
  padding-right: 0;

}
nav.navbar {
  margin-left: 0;
  padding-left: 15px;
  padding-top: 0;
  padding-bottom: 0;
  padding-right: 0;
}
.brand-wrapper {
  padding: 0;
}
.brand {
  color: #007bff;
  font-size: 30px;
  margin-right: 4px;
  padding-right: 8px;
}
.mdb-title {
  font-size: 24px;
  padding-top: 11px;
  padding-bottom: 5px;
}
.navbar-text {
  margin-bottom: 0;
}
.nav button {
  margin-left: 2px;
  margin-right: 2px;
}
.navbar-btn {
  text-transform: uppercase;
  margin-top: 12px;
  padding-top: 0;
  padding-bottom: 0;
  border: 0;
  height: 25px;
  margin-left: 7px !important;
  margin-right: 7px !important;
}
.time-remaining {
  font-size: 24px;
  padding-left: 8px;
}

.phase-nav-pane {
  background-color: #666666ff;
  height: 100%;
  padding: 0;
}

.phase-nav {
  padding-bottom: 8px;
  /* padding-bottom: 0; */
}
.phase-nav.no-children {
  padding-bottom: 0;
}

.phase-nav button {
  width: 100%;
  background-color: transparent;
  text-align: left;
  border: 0;
  padding-top: 6px;
  padding-bottom: 6px;
  padding-left: 5px;
  padding-right: 0 !important;
  /* font-size: 1.1em; */
  font-size: 15.8px;
  font-weight: bold;
  color: white;
}

.phase-label button {
  color: white;
  /* font-size: 1.1em; */
  font-size: 15.8px;
  line-height: 1.2em;
  font-weight: bold;
  padding-bottom: 0;
  padding-right: 0 !important;
  /* padding-top: 4px; */
}

.subphase-nav button {
  color: white;
  padding-top: 2.5px;
  padding-bottom: 2.5px;
  /* padding-left: 15px; */
  font-size: 0.75em;
  font-weight: 300;
  text-transform: uppercase;
}


.phase-details {
  height: 100%;
  padding-left: 0;
  padding-right: 0;
  padding-bottom: 0;
  overflow:hidden;
}

.phase-container {
  height: 100%;
}

.phase-row {
  height: 100%;
  overflow: hidden;
}

.phase-col {
  height: 100%;
  padding-left: 0;
  padding-right: 0;
  overflow-y: hidden;
}
.left-col-contents {
  height: 100%;
  overflow-y: hidden;
}


.interventions-col {
  height: 100%;
  padding-left: 0;
  padding-right: 0;
  overflow-y: scroll;
}


