.patient-status-row {
  width: 100%;
  height: fit-content;
  overflow-y: hidden;
}
.patient-status-col {
  height: 100%;
  overflow-y: scroll;
}
ul.interventions-outline {
  height: 100%;
  overflow-y: scroll;
}


hr.minimal {
  margin-top: 8px;
  margin-bottom: 8px;
  border-top-width: 2px;
}

ul {
  list-style: none;
  padding-left: 20px;
  margin: 0px;
}
ul.first {
  padding-left: 5px;
}


li.menu-depth-1 {
  padding-left: 0;
}
li.menu-depth-2 {
  padding-left: 20px;
}
li.menu-depth-3 {
  padding-left: 40px;

}
li.menu-depth-4 {
  padding-left: 60px;
}

li.menu-heading {
  font-weight: bold;
  font-size: 0.85em;
}
li.intervention button {
  font-size: 0.75em;
}

.intervention-submenu {
  font-size: 15.8px;
  padding-top: 4px;
  padding-bottom: 4px;
}
.intervention-outline-wrapper {
  padding-bottom: 5px;
}
.nav-tabs {
  width: 100%;
}

ul.nav-tabs a.nav-link {
  padding-left: 6px;
  padding-right: 6px;
  padding-top: 4px;
  padding-bottom: 4px;
}

.intervention-btn {
  text-align: left;
}

.checkbox-icon {
  transform: translateY(4px);
  font-size: medium;
  margin-right: 4px;
}
.callout-icon {
  margin-left: 10px;
  font-size: medium;
  transform: translateY(5px);
}
.chevron-icon {
  transform: translateY(5px)
}
.timer-icon {
  transform: translateY(8px)
}

.callout {
  margin-left: 10px;
  /* color: blue; */
  font-size: 0.75em;
  font-style: italic;
  overflow: none;
  text-overflow: ellipsis;
}
.callout.currentCallOut {
  background-color: yellow;
}

.card {
  border: 0;
}
/* .card {
  height: 100%;
  overflow-y: hidden;
} */
.card-body {
  padding-top: 5px;
  /* margin-bottom: 20px; */
  height: 110%;
  overflow-y: scroll;
  border: 0;
  /* overflow-y: scroll; */
}
.collapse {
  height: 100%;
}
.interventions-card {
  height: fit-content;
  border: 0;
}
.interventions-card-header {
  background-color: white;
  text-align: left;
  font-weight: bold;
  font-size: larger;
}
.interventions-card-body {
  height: calc(100% -53px);
  /* height: fit-content; */
  padding-left: 0;
  padding-right: 0;
  padding-top: 0;
  padding-bottom: 0;
  /* margin-bottom: 40px; */
  overflow-y: scroll;
}
.checklist-card-header {
  background-color: white;
  text-align: left;
  font-weight: bold;
  font-size: larger;
}
.interventions-performed-header {
  background-color: white;
  border: 0;
  text-align: left;
  font-weight: bold;
  font-size: larger;
}
.vert-tabs-container {
  height: 100%;
  padding-left: 0;
  padding-right: 0;
  padding-bottom: 20px;
  overflow: hidden;
}

.prompt-list {
  padding-left: 0;
}
.prompt-label {
  color: #2980b9;
  font-size: 0.9em;
}
.prompt-option {
  font-size: 0.75em;
  padding-right: 5px;
}
input[type=radio].prompt-radio-btn {
  margin-right: 5px;
}
.checkbox-group-options {
  padding-left: 0;
}
.clear-radio-btn {
  padding-top: 1px;
  padding-bottom: 1px;
  padding-left: 3px;
  padding-right: 3px;
}
