.vert-tabs .intv-cat-btn {
  height: 100%;
  width: 100%;
  padding-top: 6px;
  padding-bottom: 6px;
  background-color: transparent;
  border: 1px solid #abb2b9;
  border-top-left-radius: 10px;
  border-bottom-left-radius: 10px;
  border-top-right-radius: 0px;
  text-align: center;
}

.intv-cat-btn.active {
  border: 1px solid black;
  border-right: 0px;
  background-color: blue;
  color: white;
}

ul.interventions-outline {
  height: 100%;
  /* margin-bottom: 40px !important; */
  padding-left: 0;
  overflow-y: scroll;
}

.hidden {
  display: none;
}

.tab-content {
  height: 100%;
}

.tab-pane {
  overflow-y: auto;
}


.dev {
  border: 1px dashed hotpink;
}



.hoverable.active {
  filter: invert(57%) sepia(92%) saturate(4739%) hue-rotate(342deg) brightness(108%) contrast(101%);
}

.hoverable:hover {
  filter: invert(78%) sepia(44%) saturate(1107%) hue-rotate(164deg) brightness(102%) contrast(97%);
}
